/* eslint-disable no-underscore-dangle */
import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';

// REDUX
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';

// ACTION
import * as tableActions from '../store/modules/table';

// COMPONENT
import DialogSaleEditComponent from '../components/DialogSaleEditComponent';
import DialogSpendEditComponent from '../components/DialogSpendEditComponent';

class DialogEditContainer extends PureComponent {
  static propTypes = {
    open: PropTypes.bool.isRequired,
    onClose: PropTypes.func.isRequired,
    type: PropTypes.string.isRequired,
    TableActions: PropTypes.objectOf(Function).isRequired,
  };

  constructor(props) {
    super(props);

    this.state = {
      data: {},
    };
  }

  static getDerivedStateFromProps(nextProps, prevState) {
    if (nextProps.data._id !== prevState.data._id) {
      return { data: nextProps.data };
    }
    return null;
  }

  handleChange = name => event => {
    const { data } = this.state;
    this.setState({
      data: {
        ...data,
        [name]: event.target.value,
      },
    });
  };

  checkChange = name => event => {
    const { data } = this.state;
    this.setState({
      data: {
        ...data,
        [name]: event.target.checked,
      },
    });
  };

  render() {
    const { open, onClose, type, TableActions } = this.props;
    const { data } = this.state;
    const { handleChange, checkChange } = this;
    return (
      <div>
        {type === 'sales' ? (
          <DialogSaleEditComponent
            open={open}
            onClose={onClose}
            data={data}
            handleChange={handleChange}
            checkChange={checkChange}
            createSales={() => {
              TableActions.createRow('/api/sales', data);
              onClose();
            }}
            editSales={() => {
              TableActions.editRow('/api/sales', data);
              onClose();
            }}
          />
        ) : (
          <DialogSpendEditComponent
            open={open}
            onClose={onClose}
            data={data}
            handleChange={handleChange}
            createSpends={() => {
              TableActions.createRow('/api/spends', data);
              onClose();
            }}
            editSpends={() => {
              TableActions.editRow('/api/spends', data);
              onClose();
            }}
          />
        )}
      </div>
    );
  }
}

const mapDispatchToProps = dispatch => ({
  TableActions: bindActionCreators(tableActions, dispatch),
});

export default connect(
  null,
  mapDispatchToProps,
)(DialogEditContainer);
